*{
  font-family: Helvetica, Arial, sans-serif;
}

.contenedor {
  padding: 15px;

  
}

input{
  font-size: 1.2rem;
  color: gray;
  max-width: 70%;
  margin-right: 70px;
}

.card {
  align-content: center;
  border: 1px solid gray;
  border-radius: 6px;
  overflow: hidden;
  margin-bottom: 10px;
  margin-right: 10px;
  margin-top: 10px;
}

.card p{
  padding: 5px;
  text-align: center;

}

.card img{  
  max-height: 170px;
  width: 100%;
}

.card-grid {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
}